// TITLE: SETUP AXIOS
// Create a new file to set up Axios. 
// This file will be like your helpers official guide on how to behave. 
// Name this file api.js or axiosConfig.js.

// api.js
import axios from "axios";
import axiosRetry from "axios-retry";






// Create an instance of Axios with default settings
const api = axios.create({
                baseURL: "http://localhost:3999",   // LOCAL Domains:- This is your servers base URL
                // baseURL: "https://192.168.238.113:8000",   // LOCAL Domains:- This is your servers base URL            
                // baseURL: "https://angry-houses-occur.loca.lt",   // HTTPS Domains:- This is your servers base URL
                timeout: 20000, // Optional: Maximum time to wait for a response                
                // withCredentials: true, // If cookies or sessions are needed
                headers: {
                  'Content-Type': 'application/json',
                },
            });

            // Enable retries with axios
            axiosRetry(axios, { retries: 3, retryDelay: axiosRetry.exponentialDelay });
            
export default api;